@import '../../scss/abstracts/variables';
@import '../../scss/abstracts/mixins';
$clr1:rgb(24, 25, 26);

.chat-box {
    width: 30vw;

    .chat-header {
        width: 80%;
        display: flex;
        justify-content: center;
        align-self: center;
        border-bottom: 1px solid white;
        border-top: 1px solid white;

        h2 {
            font-size: 4rem;
            align-self: center;
            line-height: 40px;
        }
    }

    &__container {
        width: 100%;
        overflow-y: scroll;
        padding: 0 10px;
        height: 65vh;

        &::-webkit-scrollbar-track {
            box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        }

        &::-webkit-scrollbar {
            width: 1rem;
        }

        &::-webkit-scrollbar-thumb {
            background-color: transparent;
            outline: 1px solid transparent;
        }

        &::-webkit-scrollbar-corner {
            background: rgba(0, 0, 0, 0);
        }
    }

    .currUser {
        align-items: flex-end;

    }

    &__msg {
        flex-direction: column;

    }

    &__img {
        margin-right: 5px;
    }

    &__username {
        margin-right: 5px;
    }

    &__sender {
        align-items: center;
        margin-bottom: 3px;
    }

    &__time {
        font-weight: 300;
    }

    &__txt {
        margin-bottom: 3px;
        background: $color-secondary;
        border-radius: 0 10px 10px 10px;
        padding: 5px 10px;
        width: fit-content;
        margin: 5px 10px;
    }

    .user-text {
        border-radius: 10px 0 10px 10px;
        // background: darken($color-secondary, 20%);
        background: rgb(66, 105, 54)
    }

    &__img {
        width: 3rem;
        height: 3rem;
        border-radius: 50%;
    }
}

.users-list {
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-around;

}

.user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;

    img {
        width: 50px;
        border-radius: 50%;
    }
}

.chat-box__mobile {
    width: 100%;
    height: 55vh;
}

.text-form {
    width: 100%;
    justify-content: center;
    margin-top: 10px;

    // @include respon(phone) {
    //     position: absolute;
    //     bottom: 0;
    // }

    @include respon(tab-port){
       justify-content: flex-start;
       margin-left: 10px;
    }
    .MuiInputBase-root {
        height: 30px;
        width: 80%;
        color:  white;
        font-size: 2rem;
        // border: 1px solid lighten($color: $clr1, $amount: 20%);
        border-bottom: 1px white solid;
        outline: none;
        padding: 10px;

        @include respon(tab-port){
          
        }
        &:after {
            border-bottom: none;
        }

        input {
            ::-webkit-input-placeholder {
                color: white;
            }
        }

       

    }

    button {
        height: 30px;
        border-left: none;
        background-color: $clr1;
        border: 1px solid lighten($color: $clr1, $amount: 20%);
        outline: none;
        color: white;
        width: 20%;
    }
}


@media (max-width:850px) {
    .chat-box__container {
        overflow-y: scroll;
        height: 50vh;
        position: relative;
    }
}

@media (max-width:400px) {
    .chat-box__container {
        overflow-y: scroll;
        height: 42vh;
        position: relative;
    }
}